@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-image: #fff;
  min-height: 100vh;
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

nav {
  font-family: "Pacifico", cursive;
  margin-left: 20px;
  font-size: 2rem;
  display: inline-block;
  cursor: pointer;
}

.links {
  color: #000;
  text-decoration: none;
}

.search-form-center {
  background-color: #000;
}

.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 10vh;
  box-shadow: inset;
}
.center {
  margin-top: 30vh;
}
.search-bar {
  border: 1px solid #000;
  width: 50%;
  padding: 10px;
}
.search-button {
  font-weight: 600;
  border: none;
  margin-left: 10px;
  padding: 10px;
  background-color: #ff8000;
  color: #fff;
}

h2 {
  color: grey;
  margin-left: 20px;
  font-weight: 300;
}

.recipes {
  margin: 0 auto;
  background: #fff;
  font-family: "Roboto";
  font-weight: lighter;
  margin-top: 10vh;
  height: 100%;
  width: 90%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.recipe {
  background: #e2e1e0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  border: 1px solid #f1f1f1;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  margin: 20px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

img {
  width: 100%;
  object-fit: contain;
}

.recipe h1 {
  text-align: center;
  min-height: 10%;
  font-size: 1rem;
  font-weight: 300;
}

@media only screen and (max-width: 600px) {
  .recipe {
    flex-direction: row;
    height: 100px;
    width: 80%;
    margin: 5px auto;
  }
  img {
    background-color: #fff;
    height: 100px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  .recipe h1 {
    margin-top: 10px;
    text-align: center;
    width: 50%;
    min-height: 100%;
  }
}

@media only screen and (max-width: 600px) {
  .recipes {
    grid-template-columns: 1fr;
  }
}

.description {
  background-color: #e2e1e0;
  margin: 10vh auto 0 auto;
  width: 50vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
}

.description img {
  background-color: #e2e1e0;

  margin-top: 5vh;
  height: 300px;
  width: 300px;
}

@media only screen and (max-width: 600px) {
  .description {
    width: 100vw;
    margin: 0;
    border-radius: 0;
  }

  .description img {
    height: 100px;
    width: 80%;
    object-fit: contain;
  }

  .description p {
    line-height: normal;
  }
}

